export default {
  name: "Login",
  data(){
    return{
      rememberMe : false,
      form : {
        username : '',
        email : '',
        password:''
      }
    }
  },
  methods:{
    async Register(){
      await this.$api.post('register' , this.form).catch( err => console.log(err))
      await this.$auth.loginWith('local', {
        data: {
          username: this.form.username,
          password: this.form.password
        }
      })
      this.form = {
        username: '',
        password: '',
        email : '',
      }
      this.$router.push({path: '/'})
    }
  }
}
